<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="importdataVisible.visible"
      width="800px"
      @close="resetBtnFuc"
    >
      <div v-if="firstShow">
        <div class="importbox">
          <span class="num">1</span>
          <div class="con">
            <p class="tt"></p>
            <div class="ts">
              <p></p>
            </div>
            <el-button type="primary" size="small" @click="downMoudle"></el-button>
          </div>
        </div>
        <div class="importbox">
          <span class="num">2</span>
          <div class="con">
            <p class="tt"></p>
            <div class="ts">
              <p>
                <i></i> xlsx
              </p>
              <p>
                <i></i> 
              </p>
              <p>
                <i></i> 
              </p>
              <p v-if="showYearbox">
                <i></i> 
              </p>
            </div>
            <div class="yearbox" v-if="showYearbox">
              <p>
                <i>*</i> 
              </p>
              <el-date-picker
                v-model="year"
                value-format="yyyy"
                type="year"
                placeholder=""
                @change="hasYear"
                size="small"
              ></el-date-picker>
            </div>
            <el-upload
              v-if="upfileBtn"
              class="upload-demo"
              :action="importFileList"
              :on-success="dataSuccess"
              :file-list="fileList"
              :show-file-list="false"
              accept=".xls, .xlsx"
              :data="{year:year}"
              :before-upload="beforeUpload"
              :multiple="false"
              :limit="1"
              :auto-upload="true"
              ref="uploaddata"
            >
              <el-button size="small" type="primary" :disabled="updisabled"></el-button>
            </el-upload>
            <div class="filename" v-if="showFilename">
              <a class="name" href="javascript:;">{{filename}}</a>
              <el-upload
                class="re-upload"
                :action="importFileList"
                :show-file-list="false"
                accept=".xls, .xlsx"
                :data="{year:year}"
                :on-success="dataTwoSuccess"
                :before-upload="beforeUpload"
              >
                <a href="javascript:;"></a>
              </el-upload>
            </div>
            <p class="error" v-if="errorShow" :style="errorBg"></p>
            <p class="success" v-if="successShow" :style="successBg">,3s</p>
          </div>
        </div>
      </div>
      <div class="nopassbox" v-if="nopassShow">
        <p class="tt1">
          
          <span>{{allNumber}}</span> 
          <span>{{successNumber}}</span> 
        </p>
        <p class="tt2">
          
          <span>{{errorNumber}}</span> 
        </p>
        <el-table border :data="nopassList" highlight-current-row style="width: 100%;" height="400px">
          <el-table-column prop="rowNum" label="" width="80px"></el-table-column>
          <el-table-column prop="columnHead" label="" width="160px"></el-table-column>
          <el-table-column prop="cellMessage" label=""></el-table-column>
          <el-table-column prop="errorMessage" label=""></el-table-column>
        </el-table>
        <p class="error" v-if="errorReShow" :style="errorBg"></p>
        <p class="success" v-if="successReShow" :style="successBg"></p>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-upload
          v-if="updateBtnShow"
          class="re-upload"
          :data="{year:year}"
          :action="importFileList"
          :show-file-list="false"
          accept=".xls, .xlsx"
          :on-success="handleSigleSuccess"
          :before-upload="beforeUpload"
        >
          <a href="javascript:;"></a>
        </el-upload>

        <!-- <el-button
          size="small"
          type="primary"
          :disabled="sureImport"
          @click="showMessagePage"
          v-if="oneSure"
        ></el-button>-->
        <el-button
          v-if="lastSure"
          size="small"
          type="primary"
          :disabled="sureImportLast"
          @click="sureImportData"
        ></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { upfile, imgIp, importProjectList } from "../api/imgUrl";
import { defaturl } from "../api/apiUrl";
import {
  projectImportFileData,
  threePlanImportFileData,
  keyWorkImportFileData,
  keyAreaImportFileData,
  keyItemImportFileData,
  zhongWorkImportFileData
} from "../api/api";
export default {
  components: {},
  props: [
    "importdataVisible",
    "itemShow",
    "showYearbox",
    "upbtnDisabled",
    "importFileList"
  ],
  data() {
    return {
      userid: JSON.parse(this.$store.getters.userdata).id,
      updisabled: this.upbtnDisabled,
      fileList: [],
      errorBg: {
        backgroundImage: "url(" + require("../assets/jg.png") + ")"
      },
      successBg: {
        backgroundImage: "url(" + require("../assets/suc.png") + ")"
      },
      allNumber: 0, //
      successNumber: 0, //
      errorNumber: 0, //
      errorShow: false, //
      successShow: false, //
      upfile: "",
      imgIp: "",
      defaturl: "",
      importProjectList: "",
      // 
      firstShow: true,
      // 
      nopassShow: false,
      // 
      updateBtnShow: false,
      oneSure: true, //
      lastSure: false, //
      sureImport: true, //
      sureImportLast: true, //
      haspassList: [], //
      nopassList: [], //
      errorReShow: false,
      successReShow: false,
      // 
      upfileBtn: true,
      // 
      showFilename: false,
      filename: "",
      year: ""
    };
  },
  created() {
    this.upfile = upfile;
    this.imgIp = imgIp;
    this.defaturl = defaturl;
    this.importProjectList = importProjectList;
  },
  watch: {
    importdataVisible: function(newValue, oldValue) {
      this.$emit("update:importdataVisible", newValue);
    },
    upbtnDisabled: function(newValue, oldValue) {
      this.$emit("update:upbtnDisabled", newValue);
    }
  },
  methods: {
    // 
    showMessagePage() {
      this.firstShow = false;
      this.nopassShow = true;
      this.lastSure = true;
      this.oneSure = false;
      this.updateBtnShow = true;
      if (this.successNumber == 0) {
        this.sureImportLast = true;
      } else {
        this.sureImportLast = false;
      }
    },
    // 
    sureImportData() {
      this.importdataVisible.visible = false;
      // 
      if (this.itemShow == 1) {
        let para = {
          passList: this.haspassList,
          noPassList: this.nopassList
        };
        para = JSON.stringify(para);
        projectImportFileData(para, data => {
          this.$message({
            type: "success",
            message: "!"
          });
          this.$emit("getAllList-event");
          this.resetBtnFuc();
        });
      } else if (this.itemShow == 3) {
        //   
        let para = {
          passThreeList: this.haspassList,
          noPassList: this.nopassList,
          passList: [],
          year: this.year,
          typemodel: 1
        };
        para = JSON.stringify(para);
        threePlanImportFileData(para, data => {
          this.$message({
            type: "success",
            message: "!"
          });
          this.$emit("getAllList-event");
          this.resetBtnFuc();
        });
      } else if (this.itemShow == 4) {
        //   
        let para = {
          passThreeList: this.haspassList,
          noPassList: this.nopassList,
          passList: [],
          year: this.year,
          typemodel: 2
        };
        para = JSON.stringify(para);
        threePlanImportFileData(para, data => {
          this.$message({
            type: "success",
            message: "!"
          });
          this.$emit("getAllList-event");
          this.resetBtnFuc();
        });
      } else if (this.itemShow == 7) {
        //   
        let para = {
          passTripleWorkList: this.haspassList,
          noPassList: this.nopassList,
          year: this.year,
          userid: this.userid
        };
        para = JSON.stringify(para);
        keyWorkImportFileData(para, data => {
          this.$message({
            type: "success",
            message: "!"
          });
          this.$emit("getAllList-event");
          this.resetBtnFuc();
        });
      } else if (this.itemShow == 8) {
        //   
        let para = {
          passTripleAreaList: this.haspassList,
          noPassList: this.nopassList,
          year: this.year,
          userid: this.userid
        };
        para = JSON.stringify(para);
        keyAreaImportFileData(para, data => {
          this.$message({
            type: "success",
            message: "!"
          });
          this.$emit("getAllList-event");
          this.resetBtnFuc();
        });
      } else if (this.itemShow == 9) {
        //   
        let para = {
          importSituationList: this.haspassList,
          noPassList: this.nopassList,
          year: this.year,
          userid: this.userid
        };
        para = JSON.stringify(para);
        keyItemImportFileData(para, data => {
          this.$message({
            type: "success",
            message: "!"
          });
          this.$emit("getAllList-event");
          this.resetBtnFuc();
        });
      } else if (this.itemShow == 10) {
        //   
        let para = {
          passPointList: this.haspassList,
          noPassList: this.nopassList,
          year: this.year
        };
        para = JSON.stringify(para);
        zhongWorkImportFileData(para, data => {
          this.$message({
            type: "success",
            message: "!"
          });
          this.$emit("getAllList-event");
          this.resetBtnFuc();
        });
      }
    },
    // 
    downMoudle() {
      if (this.itemShow == 1) {
        window.open(this.defaturl + "/templateDownload/projectTemplateDown");
      } else if (this.itemShow == 3) {
        window.open(this.defaturl + "/templateDownload/threeTemplateDown");
      } else if (this.itemShow == 4) {
        window.open(this.defaturl + "/templateDownload/leaderTemplateDown");
      } else if (this.itemShow == 10) {
        window.open(this.defaturl + "/templateDownload/pointTemplateDown");
      } else if (this.itemShow == 7) {
        window.open(
          this.defaturl + "/templateDownload/tripleWorkImportTemplate"
        );
      } else if (this.itemShow == 8) {
        window.open(
          this.defaturl + "/templateDownload/tripleAreaImportTemplate"
        );
      } else if (this.itemShow == 9) {
        window.open(this.defaturl + "/templateDownload/importSituationDown");
      }
    },
    // 
    handleSigleSuccess(file) {
      if (file.code == -1) {
        this.errorReShow = true;
        this.successReShow = false;
        this.errorNumber = 0;
        this.successNumber = 0;
        this.sureImportLast = true;
        this.allNumber = 0;
        this.haspassList = [];
        this.nopassList = [];
      } else {
        this.errorReShow = false;
        this.successReShow = true;
        if (this.itemShow == 1) {
          this.haspassList = file.obj.passList;
        } else if (this.itemShow == 3 || this.itemShow == 4) {
          this.haspassList = file.obj.passThreeList;
        } else if (this.itemShow == 7) {
          this.haspassList = file.obj.passTripleWorkList;
        } else if (this.itemShow == 8) {
          this.haspassList = file.obj.passTripleAreaList;
        } else if (this.itemShow == 9) {
          this.haspassList = file.obj.importSituationList;
        } else if (this.itemShow == 10) {
          this.haspassList = file.obj.passPointList;
        }
        if (this.haspassList == null) {
          this.haspassList = [];
        }
        this.successNumber = this.haspassList.length;
        this.errorNumber = file.obj.noPassList.length;
        this.allNumber = this.errorNumber + this.successNumber;
        if (this.errorNumber == 0) {
          this.nopassList = [];
        } else {
          this.nopassList = file.obj.noPassList;
        }
        if (this.successNumber == 0) {
          this.sureImportLast = true;
        } else {
          this.sureImportLast = false;
        }
      }
    },
    // 
    dataTwoSuccess(file, fileList) {
      this.filename = fileList.name;
      if (file.code == -1) {
        this.errorShow = true;
        this.successShow = false;
        this.sureImport = true;
        this.nopassList = [];
        this.haspassList = [];
        this.sureImportLast = true;
      } else {
        this.errorShow = false;
        this.successShow = true;
        this.sureImport = false;
        if (this.itemShow == 1) {
          this.haspassList = file.obj.passList;
        } else if (this.itemShow == 3 || this.itemShow == 4) {
          this.haspassList = file.obj.passThreeList;
        } else if (this.itemShow == 7) {
          this.haspassList = file.obj.passTripleWorkList;
        } else if (this.itemShow == 8) {
          this.haspassList = file.obj.passTripleAreaList;
        } else if (this.itemShow == 9) {
          this.haspassList = file.obj.importSituationList;
        } else if (this.itemShow == 10) {
          this.haspassList = file.obj.passPointList;
        }
        if (this.haspassList == null) {
          this.haspassList = [];
        }
        this.successNumber = this.haspassList.length;
        this.errorNumber = file.obj.noPassList.length;
        this.allNumber = this.errorNumber + this.successNumber;
        if (this.errorNumber == 0) {
          this.nopassList = [];
        } else {
          this.nopassList = file.obj.noPassList;
        }
        if (this.successNumber == 0) {
          this.sureImportLast = true;
        } else {
          this.sureImportLast = false;
        }
        setTimeout(() => {
          this.showMessagePage();
        }, 2000);
      }
    },
    // 
    dataSuccess(file, fileList) {
      this.filename = fileList.name;
      this.upfileBtn = false;
      this.showFilename = true;
      if (file.code == -1) {
        this.errorShow = true;
        this.successShow = false;
        this.sureImport = true;
        this.nopassList = [];
        this.haspassList = [];
      } else {
        this.errorShow = false;
        this.successShow = true;
        this.sureImport = false;
        if (this.itemShow == 1) {
          this.haspassList = file.obj.passList;
        } else if (this.itemShow == 3 || this.itemShow == 4) {
          this.haspassList = file.obj.passThreeList;
        } else if (this.itemShow == 7) {
          this.haspassList = file.obj.passTripleWorkList;
        } else if (this.itemShow == 8) {
          this.haspassList = file.obj.passTripleAreaList;
        } else if (this.itemShow == 9) {
          this.haspassList = file.obj.importSituationList;
        } else if (this.itemShow == 10) {
          this.haspassList = file.obj.passPointList;
        }
        if (this.haspassList == null) {
          this.haspassList = [];
        }
        this.successNumber = this.haspassList.length;
        this.errorNumber = file.obj.noPassList.length;
        this.allNumber = this.errorNumber + this.successNumber;
        if (this.errorNumber == 0) {
          this.nopassList = [];
        } else {
          this.nopassList = file.obj.noPassList;
        }
        setTimeout(() => {
          this.showMessagePage();
        }, 2000);
      }
    },
    // 
    beforeUpload(file) {
      var testmsg = file.name.substring(file.name.lastIndexOf(".") + 1);
      const extension = testmsg === "xls";
      const extension2 = testmsg === "xlsx";
      if (!extension && !extension2) {
        this.$message({
          message: " xlsxlsx!",
          type: "warning"
        });
      }
      return extension || extension2;
    },
    resetBtnFuc() {
      this.allNumber = 0;
      this.successNumber = 0;
      this.errorNumber = 0;
      this.errorShow = false;
      this.successShow = false;
      this.firstShow = true;
      this.nopassShow = false;
      this.updateBtnShow = false;
      this.oneSure = true;
      this.lastSure = false;
      this.sureImport = true;
      this.sureImportLast = true;
      this.haspassList = [];
      this.nopassList = [];
      this.errorReShow = false;
      this.successReShow = false;
      this.upfileBtn = true;
      this.showFilename = false;
    },
    hasYear() {
      this.updisabled = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.importbox {
  display: flex;
  margin-bottom: 15px;
  .yearbox {
    display: flex;
    align-items: center;
    margin-top: 15px;
    p {
      color: #333;
      font-size: 14px;
      i {
        color: #f00;
        font-style: normal;
        font-weight: bold;
        display: inline-block;
        margin-right: 2px;
      }
    }
  }
  .error {
    color: #f00;
    font-size: 14px;
    margin-top: 8px;
    padding-left: 24px;
    background-size: 20px auto;
    background-position: left center;
    background-repeat: no-repeat;
  }
  .success {
    color: #91d466;
    font-size: 14px;
    margin-top: 8px;
    padding-left: 24px;
    background-size: 20px auto;
    background-position: left center;
    background-repeat: no-repeat;
  }
  .num {
    display: block;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: #afd7fe;
    color: #333;
    font-size: 14px;
    text-align: center;
    line-height: 24px;
    flex-shrink: 0;
  }
  .con {
    padding-left: 6px;
    flex: 1;
    .el-form {
      margin-top: 15px;
      .el-form-item--mini.el-form-item,
      .el-form-item--small.el-form-item {
        margin-bottom: 5px;
      }
    }
    .el-button {
      margin-top: 10px;
    }
    .tt {
      color: #333;
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 5px;
    }
    .ts {
      color: #999;
      font-size: 14px;
      line-height: 1.8;
      i {
        color: #99ccff;
        display: inline-block;
        margin-right: 2px;
      }
    }
  }
  .filename {
    display: flex;
    justify-content: space-between;
    background: #f4f4f4;
    padding: 5px 15px;
    margin-top: 10px;
    .name {
      color: #555;
      font-size: 14px;
    }
  }
}
.nopassbox {
  .error {
    color: #f00;
    font-size: 14px;
    margin-top: 8px;
    padding-left: 24px;
    background-size: 20px auto;
    background-position: left center;
    background-repeat: no-repeat;
  }
  .success {
    color: #91d466;
    font-size: 14px;
    margin-top: 8px;
    padding-left: 24px;
    background-size: 20px auto;
    background-position: left center;
    background-repeat: no-repeat;
  }
  .tt1 {
    color: #333;
    font-size: 14px;
    margin-bottom: 6px;
    span {
      font-weight: bold;
    }
  }
  .tt2 {
    color: #999;
    font-size: 14px;
    margin-bottom: 15px;
    span {
      font-weight: bold;
      color: #f00;
    }
  }
}
.dialog-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  .re-upload {
    margin-right: 20px;
  }
}
</style>



