<template>
  <div class="batchrule">
    <p class="navtitle">
      <span></span>
    </p>
    <div class="batch-content">
      <div class="batch-btns">
        <el-button class="batch-back" @click="goback" size="small" type="warning"></el-button>
        <el-button class="addrule" @click="addRuleBtn" size="small" type="primary"> </el-button>
      </div>
      <el-table
        class="tableinfo"
        border
        :data="ruleList"
        highlight-current-row
        empty-text=""
        :height="batchHeight"
      >
        <el-table-column type="index" label="" width="50"></el-table-column>
        <el-table-column prop="stagename" label=""></el-table-column>
        <el-table-column prop="createtime" label="">
          <template slot-scope="scope">
            <span>{{scope.row.createtime | dateformat('YYYY-MM-DD HH:mm:ss')}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="cycletype" label="">
          <template slot-scope="scope">
            <span v-html="ctypeFormat(scope,scope.row.cycletype)"></span>
          </template>
        </el-table-column>
        <el-table-column prop="cycletime" label="">
          <template slot-scope="scope">
            <!--  -->
            <p v-if="scope.row.cycletype==1">{{scope.row.cycletime}}</p>
            <!--  -->
            <p v-if="scope.row.cycletype==2">{{scope.row.cycletime}}</p>
            <!--  -->
            <p v-else-if="scope.row.cycletype==3">{{scope.row.cycletime}}</p>
            <!--  -->
            <p v-else-if="scope.row.cycletype==4">{{scope.row.cycletime}}</p>
            <!--  -->
            <p v-else-if="scope.row.cycletype==5">{{scope.row.cycletime}}</p>
          </template>
        </el-table-column>
        <el-table-column prop="warnday" width="110px" label="()"></el-table-column>
        <el-table-column prop="starttime" label="">
          <template slot-scope="scope">
            <span>{{scope.row.starttime | dateformat('YYYY-MM-DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="endtime" label="">
          <template slot-scope="scope">
            <span>{{scope.row.endtime | dateformat('YYYY-MM-DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-button class="btn" @click="editRuleBtn(scope.$index, scope.row)"></el-button>
            <!-- <el-button class="btn" @click="delRuleBtn(scope.$index, scope.row)"></el-button> -->
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <el-dialog :title="ruleTitle" :visible.sync="ruleVisible" width="800px" class="rule-dialog">
      <el-row :gutter="20">
        <el-col :span="13">
          <fieldset>
            <legend></legend>
            <div style="padding:15px;">
              <el-form
                :model="addRuleForm"
                label-width="93px"
                ref="addRuleForm"
                :append-to-body="true"
                size="small"
              >
                <el-form-item label="" prop="stagename">
                  <el-input v-model="addRuleForm.stagename" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" class="day" prop="warnday">
                  <el-input v-model="addRuleForm.warnday">
                    <template slot="append"></template>
                  </el-input>
                </el-form-item>
                <el-form-item label="" prop="cycletype">
                  <el-select
                    v-model="addRuleForm.cycletype"
                    clearable
                    placeholder=""
                    @change="cycleChange()"
                  >
                    <el-option
                      v-for="item in submitType"
                      :key="item.value"
                      :label="item.type"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-form-item>

                <!-- 1  -->
                <el-form-item label="" class="day" prop="region" v-if="dateShow1">
                  <el-input v-model="region">
                    <template slot="append"></template>
                  </el-input>
                </el-form-item>
                <!-- 2  -->
                <el-form-item label="" prop="weekday" v-if="dateShow2">
                  <el-select v-model="weekday" clearable placeholder="">
                    <el-option
                      v-for="item in weekList"
                      :key="item.value"
                      :label="item.name"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <!-- 3  -->
                <el-form-item label="" prop="monthday" v-if="dateShow3">
                  <el-select v-model="monthday" clearable placeholder="">
                    <el-option
                      v-for="item in timeday"
                      :key="item.value"
                      :label="item.name"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <!-- 4  -->
                <el-form-item label="" prop="quarter" v-if="dateShow4">
                  <el-select v-model="quarter" clearable placeholder="">
                    <el-option
                      v-for="item in timeday"
                      :key="item.value"
                      :label="item.name"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <!-- 5  -->
                <el-form-item label="" prop="daytime" v-if="dateShow5">
                  <el-date-picker
                    v-model="daytime"
                    format="yyyy-MM-dd"
                    type="date"
                    value-format="yyyy-MM-dd"
                    placeholder=""
                  ></el-date-picker>
                </el-form-item>

                <el-form-item
                  label=""
                  prop="starttime"
                  :rules="[{ required: true, message: '', trigger: 'change' }]"
                >
                  <el-date-picker
                    v-model="addRuleForm.starttime"
                    format="yyyy-MM-dd"
                    type="date"
                    value-format="yyyy-MM-dd"
                    placeholder=""
                  ></el-date-picker>
                </el-form-item>
                <el-form-item
                  label=""
                  prop="endtime"
                  :rules="[{ required: true, message: '', trigger: 'change' }]"
                >
                  <el-date-picker
                    v-model="addRuleForm.endtime"
                    format="yyyy-MM-dd"
                    type="date"
                    value-format="yyyy-MM-dd"
                    placeholder=""
                  ></el-date-picker>
                </el-form-item>
              </el-form>
            </div>
          </fieldset>
        </el-col>
        <el-col :span="11">
          <fieldset>
            <legend>{{choosenum}}</legend>
            <div class="batch-setting" style="padding:15px;height:300px;overflow:auto;">
              <el-checkbox
                :indeterminate="isIndeterminate"
                v-model="checkAll"
                @change="handleCheckAllChange"
              ></el-checkbox>
              <el-checkbox-group v-model="checkedItems" @change="handleCheckedItemsChange">
                <el-checkbox
                  v-for="item in allitems"
                  :label="item.id"
                  :key="item.id"
                  :class="classObject(item.id)?'isexist':''"
                >
                  <el-tooltip effect="dark" placement="top-start">
                    <div style="max-width:250px;line-height:2;" slot="content">{{item.name}}</div>
                    <p class="name">{{item.name}}</p>
                  </el-tooltip>
                </el-checkbox>
              </el-checkbox-group>
            </div>
          </fieldset>
        </el-col>
      </el-row>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="submitRule" type="primary">{{ruleBtn}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>
 
<script>
import {
  getAllRulelist,
  addAllRule,
  editAllRule,
  allRuleDetail,
  checkReportRules
} from "../api/projectApi";
import {
  checkPlanReportRules,
  checkImportworkReportRules
} from "../api/decisionApi";
export default {
  name: "batchrule",
  props: ["batchHeight", "allitems", "itemShow"],
  components: {},
  data() {
    return {
      ruleid: 0,
      checkAll: false,
      checkedItems: [],
      isIndeterminate: false,
      choosenum: 0,
      hasitem: [],
      ruleList: [], //
      ruleVisible: false,
      dateShow1: false, //
      dateShow2: false, //
      dateShow3: false, //
      dateShow4: false, //
      dateShow5: false, //
      region: "", //
      monthday: "", //
      quarter: "", //
      weekday: "", //
      daytime: "", //
      ruleTitle: "",
      ruleBtn: "",
      cycleNum: 0,
      addRuleForm: { cycletype: 1 }, //
      weekList: [
        { value: "1", name: "" },
        { value: "2", name: "" },
        { value: "3", name: "" },
        { value: "4", name: "" },
        { value: "5", name: "" },
        { value: "6", name: "" },
        { value: "7", name: "" }
      ],
      // 
      submitType: [
        { value: 1, type: "" },
        // { value: 2, type: "" },
        { value: 3, type: "" },
        { value: 4, type: "" },
        { value: 5, type: "" }
      ],
      // 
      timeday: [
        { value: "1", name: "1" },
        { value: "2", name: "2" },
        { value: "3", name: "3" },
        { value: "4", name: "4" },
        { value: "5", name: "5" },
        { value: "6", name: "6" },
        { value: "7", name: "7" },
        { value: "8", name: "8" },
        { value: "9", name: "9" },
        { value: "10", name: "10" },
        { value: "11", name: "11" },
        { value: "12", name: "12" },
        { value: "13", name: "13" },
        { value: "14", name: "14" },
        { value: "15", name: "15" },
        { value: "16", name: "16" },
        { value: "17", name: "17" },
        { value: "18", name: "18" },
        { value: "19", name: "19" },
        { value: "20", name: "20" },
        { value: "21", name: "21" },
        { value: "22", name: "22" },
        { value: "23", name: "23" },
        { value: "24", name: "24" },
        { value: "25", name: "25" },
        { value: "26", name: "26" },
        { value: "27", name: "27" },
        { value: "28", name: "28" },
        { value: "29", name: "29" },
        { value: "30", name: "30" },
        { value: "31", name: "31" }
      ],
      isfirst: 0
    };
  },
  created() {
    this.getRulelist();
  },
  methods: {
    // 
    getRulelist() {
      getAllRulelist({ modeltype: this.itemShow }, data => {
        this.ruleList = data.data.obj;
      });
    },
    classObject(id) {
      for (var i = 0; i < this.hasitem.length; i++) {
        if (this.hasitem[i] == id) {
          return true;
        }
      }
    },
    // 
    goback() {
      this.$emit("my-back");
    },
    // 
    ctypeFormat(scope, value) {
      switch (value) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
      }
    },
    // 
    cycleChange() {
      this.cycleNum = this.addRuleForm.cycletype;
      switch (this.cycleNum) {
        case 1:
          this.dateShow1 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 2:
          this.dateShow2 = true;
          this.dateShow1 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 3:
          this.dateShow3 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 4:
          this.dateShow4 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow5 = false;
          break;
        case 5:
          this.dateShow5 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow1 = false;
          break;
      }
    },
    // 
    addRuleBtn() {
      this.ruleVisible = true;
      this.ruleTitle = "";
      this.ruleBtn = "";
      this.changeNum = "add";
      this.region = "";
      this.monthday = "";
      this.quarter = "";
      this.weekday = "";
      this.daytime = "";
      this.addRuleForm = { cycletype: 1 };
      this.cycleChange();
      this.checkedItems = [];
      this.isfirst = 0;
    },
    // 
    editRuleBtn(index, row) {
      this.isfirst = 0;
      this.ruleid = row.id;
      this.hasitem = [];
      this.checkedItems = [];
      allRuleDetail({ aid: row.id }, data => {
        var haslist = data.data.obj.yesList;
        this.choosenum = haslist.length;
        if (this.choosenum == 0) {
          this.checkedItems = [];
        } else {
          for (var i = 0; i < haslist.length; i++) {
            this.checkedItems.push(haslist[i].id);
          }
        }
        var ctype = row.cycletype;
        this.cycleNum = row.cycletype;
        switch (ctype) {
          case 1:
            this.dateShow1 = true;
            this.dateShow2 = false;
            this.dateShow3 = false;
            this.dateShow4 = false;
            this.dateShow5 = false;
            this.region = row.cycletime;
            break;
          case 2:
            this.dateShow2 = true;
            this.dateShow1 = false;
            this.dateShow3 = false;
            this.dateShow4 = false;
            this.dateShow5 = false;
            this.weekday = row.cycletime;
            break;
          case 3:
            this.dateShow3 = true;
            this.dateShow1 = false;
            this.dateShow2 = false;
            this.dateShow4 = false;
            this.dateShow5 = false;
            this.monthday = row.cycletime;
            break;
          case 4:
            this.dateShow4 = true;
            this.dateShow1 = false;
            this.dateShow2 = false;
            this.dateShow3 = false;
            this.dateShow5 = false;
            this.quarter = row.cycletime;
            break;
          case 5:
            this.dateShow5 = true;
            this.dateShow2 = false;
            this.dateShow3 = false;
            this.dateShow4 = false;
            this.dateShow1 = false;
            this.daytime = row.cycletime;
            break;
        }
        this.ruleVisible = true;
        this.ruleTitle = "";
        this.ruleBtn = "";
        this.changeNum = "edit";
        this.addRuleForm = Object.assign({}, row);
        this.addRuleForm.endtime = new Date(this.addRuleForm.endtime);
        this.addRuleForm.starttime = new Date(this.addRuleForm.starttime);
        this.addRuleForm.endtime = new Date(this.addRuleForm.endtime);
      });
    },
    editruleFuc() {
      this.cycleNum = this.addRuleForm.cycletype;
      if (this.cycleNum == 1) {
        this.addRuleForm.cycletime = this.region; //
      } else if (this.cycleNum == 2) {
        this.addRuleForm.cycletime = this.weekday; //
      } else if (this.cycleNum == 3) {
        this.addRuleForm.cycletime = this.monthday; //
      } else if (this.cycleNum == 4) {
        this.addRuleForm.cycletime = this.quarter; //
      } else if (this.cycleNum == 5) {
        this.addRuleForm.cycletime = this.daytime; //
      }
      let para = Object.assign({}, this.addRuleForm);
      para.modeltype = this.itemShow;
      para.iDList = this.checkedItems;
      para.aid = this.ruleid;
      para = JSON.stringify(para);
      editAllRule(para, res => {
        this.ruleVisible = false;
        this.getRulelist();
        this.$message({
          type: "success",
          message: "!"
        });
      });
    },
    // 
    editRuleSubmit() {
      this.$refs.addRuleForm.validate(valid => {
        if (valid) {
          // 
          var sdate = this.addRuleForm.starttime;
          var date = new Date(sdate);
          var Y = date.getFullYear() + "-";
          var M =
            (date.getMonth() + 1 < 10
              ? "0" + (date.getMonth() + 1)
              : date.getMonth() + 1) + "-";
          var D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
          var starttime = Y + M + D;
          // 
          var edate = this.addRuleForm.endtime;
          var date2 = new Date(edate);
          var Y2 = date2.getFullYear() + "-";
          var M2 =
            (date2.getMonth() + 1 < 10
              ? "0" + (date2.getMonth() + 1)
              : date2.getMonth() + 1) + "-";
          var D2 =
            date2.getDate() < 10 ? "0" + date2.getDate() : date2.getDate();
          var endtime = Y2 + M2 + D2;

          if (this.itemShow == 1) {
            let spara = {
              pidList: this.checkedItems,
              starttime: this.addRuleForm.starttime,
              endtime: this.addRuleForm.endtime
            };
            spara = JSON.stringify(spara);
            this.conflictRuleEdit(spara);
          } else if (this.itemShow == 2) {
            this.editruleFuc();
          } else if (this.itemShow == 3) {
            let spara = {
              widList: this.checkedItems,
              starttime: this.addRuleForm.starttime,
              endtime: this.addRuleForm.endtime,
              typemodel: 1
            };
            spara = JSON.stringify(spara);
            this.conflictRuleEdit(spara);
          } else if (this.itemShow == 4) {
            let spara = {
              widList: this.checkedItems,
              starttime: this.addRuleForm.starttime,
              endtime: this.addRuleForm.endtime,
              typemodel: 2
            };
            spara = JSON.stringify(spara);
            this.conflictRuleEdit(spara);
          } else if (this.itemShow == 12) {
            let spara = {
              pidList: this.checkedItems,
              starttime: this.addRuleForm.starttime,
              endtime: this.addRuleForm.endtime
            };
            spara = JSON.stringify(spara);
            this.conflictRuleImportworkEdit(spara);
          } else {
            this.editruleFuc();
          }
        } else {
          return false;
        }
      });
    },
    // 
    conflictRuleEdit(spara) {
      checkReportRules(spara, data => {
        this.hasitem = data.data.obj;
        if (this.hasitem.length > 0) {
          this.$confirm(
            "<span style='color:#f00;'></span>",
            "",
            {
              dangerouslyUseHTMLString: true,
              type: "warning",
              center: true,
              modal: false,
              cancelButtonText: "",
              confirmButtonText: ""
            }
          )
            .then(() => {
              this.cycleNum = this.addRuleForm.cycletype;
              if (this.cycleNum == 1) {
                this.addRuleForm.cycletime = this.region; //
              } else if (this.cycleNum == 2) {
                this.addRuleForm.cycletime = this.weekday; //
              } else if (this.cycleNum == 3) {
                this.addRuleForm.cycletime = this.monthday; //
              } else if (this.cycleNum == 4) {
                this.addRuleForm.cycletime = this.quarter; //
              } else if (this.cycleNum == 5) {
                this.addRuleForm.cycletime = this.daytime; //
              }
              let para = Object.assign({}, this.addRuleForm);
              para.modeltype = this.itemShow;
              para.iDList = this.checkedItems;
              para.aid = this.ruleid;
              para = JSON.stringify(para);
              editAllRule(para, res => {
                this.ruleVisible = false;
                this.$message({
                  type: "success",
                  message: "!"
                });
                this.getRulelist();
              });
            })
            .catch(action => {});
        } else {
          this.cycleNum = this.addRuleForm.cycletype;
          if (this.cycleNum == 1) {
            this.addRuleForm.cycletime = this.region; //
          } else if (this.cycleNum == 2) {
            this.addRuleForm.cycletime = this.weekday; //
          } else if (this.cycleNum == 3) {
            this.addRuleForm.cycletime = this.monthday; //
          } else if (this.cycleNum == 4) {
            this.addRuleForm.cycletime = this.quarter; //
          } else if (this.cycleNum == 5) {
            this.addRuleForm.cycletime = this.daytime; //
          }
          let para = Object.assign({}, this.addRuleForm);
          para.modeltype = this.itemShow;
          para.iDList = this.checkedItems;
          para.aid = this.ruleid;
          para = JSON.stringify(para);
          editAllRule(para, res => {
            this.ruleVisible = false;
            this.$message({
              type: "success",
              message: "!"
            });
            this.getRulelist();
          });
        }
      });
    },
    conflictRuleImportworkEdit(spara) {
      checkImportworkReportRules(spara, data => {
        this.hasitem = data.data.obj;
        if (this.hasitem.length > 0) {
          this.$confirm(
            "<span style='color:#f00;'></span>",
            "",
            {
              dangerouslyUseHTMLString: true,
              type: "warning",
              center: true,
              modal: false,
              cancelButtonText: "",
              confirmButtonText: ""
            }
          )
            .then(() => {
              this.cycleNum = this.addRuleForm.cycletype;
              if (this.cycleNum == 1) {
                this.addRuleForm.cycletime = this.region; //
              } else if (this.cycleNum == 2) {
                this.addRuleForm.cycletime = this.weekday; //
              } else if (this.cycleNum == 3) {
                this.addRuleForm.cycletime = this.monthday; //
              } else if (this.cycleNum == 4) {
                this.addRuleForm.cycletime = this.quarter; //
              } else if (this.cycleNum == 5) {
                this.addRuleForm.cycletime = this.daytime; //
              }
              let para = Object.assign({}, this.addRuleForm);
              para.modeltype = this.itemShow;
              para.iDList = this.checkedItems;
              para.aid = this.ruleid;
              para = JSON.stringify(para);
              editAllRule(para, res => {
                this.ruleVisible = false;
                this.$message({
                  type: "success",
                  message: "!"
                });
                this.getRulelist();
              });
            })
            .catch(action => {});
        } else {
          this.cycleNum = this.addRuleForm.cycletype;
          if (this.cycleNum == 1) {
            this.addRuleForm.cycletime = this.region; //
          } else if (this.cycleNum == 2) {
            this.addRuleForm.cycletime = this.weekday; //
          } else if (this.cycleNum == 3) {
            this.addRuleForm.cycletime = this.monthday; //
          } else if (this.cycleNum == 4) {
            this.addRuleForm.cycletime = this.quarter; //
          } else if (this.cycleNum == 5) {
            this.addRuleForm.cycletime = this.daytime; //
          }
          let para = Object.assign({}, this.addRuleForm);
          para.modeltype = this.itemShow;
          para.iDList = this.checkedItems;
          para.aid = this.ruleid;
          para = JSON.stringify(para);
          editAllRule(para, res => {
            this.ruleVisible = false;
            this.$message({
              type: "success",
              message: "!"
            });
            this.getRulelist();
          });
        }
      });
    },

    conflictRule(spara) {
      checkReportRules(spara, data => {
        this.hasitem = data.data.obj;
        if (this.hasitem.length > 0) {
          this.$confirm(
            "<span style='color:#f00;'></span>",
            "",
            {
              dangerouslyUseHTMLString: true,
              type: "warning",
              center: true,
              modal: false,
              cancelButtonText: "",
              confirmButtonText: ""
            }
          )
            .then(() => {
              this.cycleNum = this.addRuleForm.cycletype;
              if (this.cycleNum == 1) {
                this.addRuleForm.cycletime = this.region; //
              } else if (this.cycleNum == 2) {
                this.addRuleForm.cycletime = this.weekday; //
              } else if (this.cycleNum == 3) {
                this.addRuleForm.cycletime = this.monthday; //
              } else if (this.cycleNum == 4) {
                this.addRuleForm.cycletime = this.quarter; //
              } else if (this.cycleNum == 5) {
                this.addRuleForm.cycletime = this.daytime; //
              }
